@charset "UTF-8";
/* ¯¯¯¯¯¯¯¯¯ *\
  ALERT
\* ˍˍˍˍˍˍˍˍˍ */
.fr-alert {
  position: relative;
  padding: 1rem 2.25rem 0.75rem 3.5rem;
  --title-spacing: 0 0 0.25rem;
  --text-spacing: 0 0 0.25rem;
}

.fr-alert::before {
  flex: 0 0 auto;
  display: inline-block;
  vertical-align: middle;
  background-color: currentColor;
  width: var(--icon-size);
  height: var(--icon-size);
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
}

.fr-alert::before {
  --icon-size: 1.5rem;
}

.fr-alert::before {
  position: absolute;
  top: 0;
  left: 0;
  margin: 1rem 0.5rem;
}

.fr-alert__title {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
}

@media (min-width: 48em) {
  .fr-alert__title {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

.fr-alert .fr-btn--close {
  position: absolute;
  top: 0.25rem;
  right: 1.25rem;
  font-size: 0.875rem;
  line-height: 1.5rem;
  min-height: 2rem;
  padding: 0.25rem 0.75rem;
  flex-direction: row;
  overflow: hidden;
  white-space: nowrap;
  max-width: 2rem;
  max-height: 2rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-weight: 500;
}

.fr-alert .fr-btn--close::before {
  --icon-size: 1rem;
}

.fr-alert .fr-btn--close::before {
  margin-left: 0;
  margin-right: 0.5rem;
}

.fr-alert + .fr-alert {
  margin-top: 1rem;
}

.fr-alert--info::before {
  content: "";
  -webkit-mask-image: url("../../icons/system/fr--info-fill.svg");
  mask-image: url("../../icons/system/fr--info-fill.svg");
}

.fr-alert--success::before {
  content: "";
  -webkit-mask-image: url("../../icons/system/fr--success-fill.svg");
  mask-image: url("../../icons/system/fr--success-fill.svg");
}

.fr-alert--error::before {
  content: "";
  -webkit-mask-image: url("../../icons/system/fr--error-fill.svg");
  mask-image: url("../../icons/system/fr--error-fill.svg");
}

.fr-alert--warning::before {
  content: "";
  -webkit-mask-image: url("../../icons/system/fr--warning-fill.svg");
  mask-image: url("../../icons/system/fr--warning-fill.svg");
}

.fr-alert--sm {
  padding: 0.5rem 2.25rem 0.25rem 3rem;
}

.fr-alert--sm::before {
  margin: 0.5rem 0.5rem;
}

.fr-alert .fr-link--close {
  position: absolute;
  top: 0.25rem;
  right: 1.25rem;
  font-size: 0.875rem;
  line-height: 1.5rem;
  min-height: 2rem;
  padding: 0.25rem 0.75rem;
  flex-direction: row;
  overflow: hidden;
  white-space: nowrap;
  max-width: 2rem;
  max-height: 2rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-weight: 500;
}

.fr-alert .fr-link--close::before {
  --icon-size: 1rem;
}

.fr-alert .fr-link--close::before {
  margin-left: 0;
  margin-right: 0.5rem;
}

.fr-alert {
  box-shadow: inset 0 0 0 1px var(--border-plain-grey), inset 2.5rem 0 0 0 var(--background-flat-grey);
}

.fr-alert::before {
  color: var(--text-inverted-grey);
}

.fr-alert--info {
  box-shadow: inset 0 0 0 1px var(--border-plain-info), inset 2.5rem 0 0 0 var(--background-flat-info);
}

.fr-alert--error {
  box-shadow: inset 0 0 0 1px var(--border-plain-error), inset 2.5rem 0 0 0 var(--background-flat-error);
}

.fr-alert--success {
  box-shadow: inset 0 0 0 1px var(--border-plain-success), inset 2.5rem 0 0 0 var(--background-flat-success);
}

.fr-alert--warning {
  box-shadow: inset 0 0 0 1px var(--border-plain-warning), inset 2.5rem 0 0 0 var(--background-flat-warning);
}
